<template>
  <div class="login">
    <!-- 登录 -->
    <div v-if="!this.$root.isLogin">
      <div class="title">豆果美食</div>
      <van-form>
        <van-field
          v-model="account"
          name="手机号"
          label="手机号"
          placeholder="请输入手机号"
          autocomplete="off"
          :rules="[{ required: true, message: '请填写手机号' }]"
        />
        <van-field
          v-model="password"
          :type="!isOpenLogin ? 'password' : 'text'"
          name="密码"
          label="密码"
          placeholder="密码"
          :right-icon="!isOpenLogin ? 'closed-eye' : 'eye-o'"
          @click-right-icon="toggleLoginPasssword"
          autocomplete="off"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px">
          <van-button
            class="btnd"
            round
            block
            type="info"
            native-type="submit"
            @click="getstorager"
            >登入</van-button
          >
        </div>
        <div style="margin: 16px">
          <van-button
            round
            block
            type="button"
            class="register"
            @click="register"
            >注册</van-button
          >
        </div>
      </van-form>
    </div>
    <div class="login-exit" v-else>
      <div class="back" @click="back">
        <van-icon name="arrow-left" />
      </div>
      <div class="sc">
        <div
          class="sc-box"
          v-for="(ite, index) in cpData"
          :key="index"
          @click="gitDetail(ite.cook_id)"
        >
          <div class="sc-box">
            <div class="sc-img">
              <img
                class="auto-img img-box"
                :src="ite.share_info.image"
                alt=""
              />
            </div>
            <div class="sc-pname">
              <div class="sc-title">
                {{ ite.title }}
              </div>
              <div class="sc-time">
                {{ ite.release_time }}
              </div>
              <div class="sc-cont">
                <div class="sc-p">
                  <img class="auto-img" :src="ite.user.user_photo" alt="" />
                </div>
                <div class="sc-name">{{ ite.user.nickname }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  name: 'Login',
  data() {
    return {
      id: '',
      isOpenLogin: false,
      name: '',
      account: '',
      password: '',
      cpData: [],
      list: [
        {
          title: '我的收藏',
        },
        {
          title: '消息中心',
        },
      ],
    }
  },

  created() {
    this.id = this.$root.scjl
    this.getSc()
  },

  methods: {
    gitDetail(id) {
      this.$router.push({ name: 'Detail', params: { id } })
    },
    getSc() {
      for (var i = 0; i < this.id.length; i++) {
        this.axios({
          method: 'get',
          url: 'https://apis.netstart.cn/douguo/recipe/detail/' + this.id[i],
        })
          .then((result) => {
            this.cpData.push(result.data.result.recipe)
          })
          .catch((err) => {})
      }
    },

    //注册
    register() {
      this.$router.push({ name: 'Register' })
    },

    //返回
    back() {
      this.$router.push({ name: 'Home' })
    },

    //登录
    getstorager() {
      let userInfo = localStorage.getItem('data')

      if (this.account === '' || this.password === '') {
        Toast('账号或者密码不能为空')
        return
      }
      if (userInfo === null) {
        Toast('账号没注册')
        return
      }
      let userInfoObj = JSON.parse(userInfo)

      if (
        this.account != userInfoObj.account ||
        this.password != userInfoObj.password
      ) {
        Toast('账号或密码输入错误')
        this.account = ''
        this.password = ''
        return
      }

      if (
        this.account === userInfoObj.account &&
        this.password === userInfoObj.password
      ) {
        Toast('登录成功')
        this.$router.push({ name: 'Home' })
        this.$root.isLogin = true
      }
    },

    toggleLoginPasssword() {
      this.isOpenLogin = !this.isOpenLogin
    },
  },
}
</script>

<style lang="less" scoped>
.sc {
  padding: 10px;
  margin-bottom: 71px;
  .sc-box {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .sc-img {
      width: 150px;
      height: 100px;
      img {
        object-fit: cover;
      }
    }
    .sc-pname {
      margin-left: 15px;
      flex: 2;
      .sc-time {
        padding: 10px 0;
      }
      .sc-title {
        word-break: break-all;

        text-overflow: ellipsis;

        display: -webkit-box;

        -webkit-box-orient: vertical;

        -webkit-line-clamp: 2; /* 这里是超出几行省略 */

        overflow: hidden;
      }
      .sc-cont {
        display: flex;
        align-items: center;
        .sc-p {
          width: 20px;
          img {
            border-radius: 50%;
          }
        }
        .sc-name {
          padding: 0 8px;
        }
      }
    }
  }
}

.login {
  .back {
    padding: 10px;
    width: 20px;
    height: 20px;
  }
  .title {
    padding: 20px;
    font-size: 30px;
    font-weight: bold;
    margin-top: 150px;
  }
  .register {
    display: block;
    border: none;
  }
}
.btnd {
  background-color: #facc30;
}

/deep/.van-button--info {
  border: #facc30;
}
</style>
